.country{
    padding: 0 50px 50px;

    @media (max-width: 335px) {
        padding: 0 10px;
    }

    nav{
        margin: 30px 0;

        button{
            width: 100px;
            height: 30px;
            border-radius: 5px;
            border: none;
            background-color: var(--Elements);
            box-shadow: 0px 0px 4px #dadae2;
            color: var(--Text);

            &:hover{
                box-shadow: 0px 0px 8px #c9c9d5;
            }
        }
    }

    >div{
        display: flex;
        width: 100%;
        align-items: center;

        img{
            max-width: 55%;
            min-width: 40%;
            box-shadow: 0 0 4px #dadae2;
            height: 100%;
            object-fit: cover;
        }
        .text-info{
            padding: 20px;
            display: flex;
            flex-direction: column;
            line-height: 25px;
            color: var(--Text);

            h3{
                @media (max-width: 769px) {
                    text-align: center;
                }
            }

            .text-grid{
                display: flex;
                flex-wrap: wrap;

                span{
                   width: 50%; 

                    @media (max-width: 769px) {
                        padding-left: 2em;
                        width: auto;
                }

                    @media (max-width: 607px) {
                        padding-left: 0;
                        text-align: center;
                }
                }

                @media (max-width: 769px) {
                    display: grid;
                    flex-wrap: nowrap;
                    grid-template-columns: 1fr 1fr;
                    grid-template-rows: repeat(4, 1fr);
                    gap: 1em;
                }

                @media (max-width: 607px) {
                    grid-template-columns: 1fr;
                }
            }
            .border{    
                margin-top: 30px;
                display: flex;

                @media (max-width: 607px) {
                    flex-direction: column;
                }
                span{
                    margin-right: 15px;

                    @media (max-width: 607px) {
                        text-align: center;
                    }
                }

                button{
                    margin-right: 20px;
                    background-color: var(--Elements);
                    border: none;
                    box-shadow: 0px 0px 4px #c7c4c4;
                    padding: 5px 25px;
                    color: var(--Text);
                    &:hover{
                        box-shadow: 0px 0px 8px #c9c9d5;
                    }
                    @media (max-width: 607px) {
                        margin-top: 20px;
                        margin-right: 0px;
                    }
                }
            }
        }

        @media (max-width: 769px) {
            flex-direction: column;
        }
    }
}